import React from 'react';
import Ratnajit from "../../Layouts/Home/Videos/Ratnajeet.mp4";

const homeyashwantImage = () => {
    return <video
        className="video-fluid z-depth-1"
        loop
        controls
        muted
        preload="none"
    >
        <source src={Ratnajit} type="video/mp4" />
    </video>
}
export default homeyashwantImage;